.friendList {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-bottom: 60px;
}

.item {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  width: 600px;
  margin: 0 auto;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
}

.item:not(:first-child) {
  margin-top: 15px;
}
